$main_color: #e7802c;
$secondary_color: rgb(218, 150, 87);

#landingPage{
  header{
      height: 100vh;
      width: 100%;
      display: table;
  }

  .background{
      top: 0;
      left: 0;
      right: 0;
      left: 0;
      height: 100vh;
      background-image: url('https://image.ibb.co/gxaUAS/header.jpg');
      background-position: 50% 0;
      opacity: .4;
      z-index: -1;
      position: absolute;
  }

  .content{
      display: table-cell;
      vertical-align: middle;
      text-align:center;
      h1{
          color: $main_color;
          font-size:4rem;
      }
      p{
          color:$secondary_color;
          font-size:2rem;
          margin-bottom: .5rem;
      }
      width: 100%;
  }

  .navbar{
      z-index: 999;
      
      bottom: 0;
      left: 0;
      width: 100%;
  }

  .navbar-light .navbar-nav .nav-link{
      font-weight: 700;
      font-size: 1.2rem;
  }

  .btn-xlarge {
      font-size: 1.6rem;
  }

  h2{
      font-size:2rem;
  }

  .row{
      margin-top:1rem;
      margin-bottom:1rem;
  }

  #generalContext{
      background-color:#f5f5f5;
      padding: 1rem 0;
  }

  hr{
      width: 10%;
      border-width: 2px;
      border-color: $main_color;
  }

  /* Timeline */
      .timeline {
          list-style: none;
          padding: 20px 0 20px;
          position: relative;
          &:before {
              top: 0;
              bottom: 0;
              position: absolute;
              content: " ";
              width: 3px;
              background-color: #eeeeee;
              left: 50%;
              margin-left: -1.5px;
          }
          li {
              margin-bottom: 20px;
              position: relative;
              &::before, &::after{
                  content: " ";
                  display: table;
              }
              &:after{
                  clear: both;
              }
              .timeline-panel {
                  width: 46%;
                  float: left;
                  border: 1px solid #d4d4d4;
                  border-radius: 2px;
                  padding: 20px;
                  position: relative;
                  -webkit-box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
                  box-shadow: 0 1px 6px rgba(0, 0, 0, 0.175);
                  &::before {
                      position: absolute;
                      top: 26px;
                      right: -15px;
                      display: inline-block;
                      border-top: 15px solid transparent;
                      border-left: 15px solid #ccc;
                      border-right: 0 solid #ccc;
                      border-bottom: 15px solid transparent;
                      content: " ";
                  }
                  &::after {
                      position: absolute;
                      top: 27px;
                      right: -14px;
                      display: inline-block;
                      border-top: 14px solid transparent;
                      border-left: 14px solid #fff;
                      border-right: 0 solid #fff;
                      border-bottom: 14px solid transparent;
                      content: " ";
                  }
              }
              .timeline-badge {
                  color: #fff;
                  width: 50px;
                  height: 50px;
                  line-height: 50px;
                  font-size: 1.4em;
                  text-align: center;
                  position: absolute;
                  top: 16px;
                  left: 50%;
                  margin-left: -25px;
                  background-color: #999999;
                  z-index: 100;
                  border-top-right-radius: 50%;
                  border-top-left-radius: 50%;
                  border-bottom-right-radius: 50%;
                  border-bottom-left-radius: 50%;
              }
          }
          li.timeline-inverted{
              .timeline-panel {
                  float: right;
                  &::before {
                      border-left-width: 0;
                      border-right-width: 15px;
                      left: -15px;
                      right: auto;
                  }
                  &::after {
                      border-left-width: 0;
                      border-right-width: 14px;
                      left: -14px;
                      right: auto;
                  }
              }
          }
      }

      .timeline-badge.primary {
          background-color: #2e6da4 !important;
      }

      .timeline-badge.success {
          background-color: #3f903f !important;
      }

      .timeline-badge.warning {
          background-color: #f0ad4e !important;
      }

      .timeline-title {
          margin-top: 0;
          color: inherit;
      }

      .timeline-body > p,
      .timeline-body > ul {
          margin-bottom: 0;
      }

          .timeline-body > p + p {
              margin-top: 5px;
          }

      @media (max-width: 767px) {
          ul.timeline:before {
              left: 40px;
          }

          ul.timeline > li > .timeline-panel {
              width: calc(100% - 90px);
              width: -moz-calc(100% - 90px);
              width: -webkit-calc(100% - 90px);
          }

          ul.timeline > li > .timeline-badge {
              left: 15px;
              margin-left: 0;
              top: 16px;
          }

          ul.timeline > li > .timeline-panel {
              float: right;
          }

              ul.timeline > li > .timeline-panel:before {
                  border-left-width: 0;
                  border-right-width: 15px;
                  left: -15px;
                  right: auto;
              }

              ul.timeline > li > .timeline-panel:after {
                  border-left-width: 0;
                  border-right-width: 14px;
                  left: -14px;
                  right: auto;
              }
      }
  /* End of timeline */

  #userAnalysis{ 
      .row>p{
        font-size: 1.2rem;
      }
  }

  #requirements{
      background-color: rgb(245, 245, 245);
      padding: 1rem 0;
  }

  #extra{
      background-color: rgb(245, 245, 245);
      padding: 1rem 0;
  }

  @media print{
    header{
        height: auto;
        width: 100%;
        display: table;
    }
    
    .background{
        background-image: none;
    }
  
    nav, #enterBtn{
        display: none;
    }
    #requirements, #generalContext, #extra{
        background-color: white;
    }
  
    .timeline {
        all:initial;
        &:before {
            all: initial;
        }
        li {
            all: initial;
            &::before, &::after{
                content: " ";
                all: initial;
            }
            &:after{
                all: initial;
            }
            .timeline-panel {
                all: initial;
                &::before {
                    all: initial;
                }
                &::after {
                    all: initial;
                }
            }
            .timeline-badge {
                all: initial;
            }
        }
        li.timeline-inverted{
            .timeline-panel {
                all: initial;;
                &::before {
                    all: initial;
                }
                &::after {
                    all: initial;
                }
            }
        }
        h4{
            font-size: 1.4rem !important;    
            margin: 1rem 0 0 0 !important;    
            display: list-item !important;
            list-style-type: disc !important; 
        }
        p{
            font-size: 1.2rem !important;    
            margin: 0 0 1rem .6rem !important;  
        }
        li{
            list-style: disc !important;    
        }
        list-style: disc  !important;
        padding: inherit;
    }
  
    .timeline-badge.primary {
        all: initial;
    }
  
    .timeline-badge.success {
        all: initial;
    }
  
    .timeline-badge.warning {
        all: initial;
    }
  
    .timeline-title {
        all: initial;
    }
  
    .timeline-body > p,
    .timeline-body > ul {
        all: initial;
    }
  
        .timeline-body > p + p {
            all: initial;
        }
  }
  
}

@page {
  size: letter;
  margin: 2.54cm 2cm;
}



